<template>
    <div>
        <div>
        <div class="top">
            <van-nav-bar>
                <template #title>
                    <span style="color:#fff">搜索</span>
                </template>
            </van-nav-bar>
        </div>
        <div class="main">
            <div class="mainTop">
                <van-search
                v-model="value"
                show-action
                placeholder="请输入搜索关键词"
                @search="onSearch"
                >
                <template #action>
                    <div @click="onSearch" class="mainTopDiv">搜索</div>
                </template>
                </van-search>
            </div>
            <div class="mainMain">
                <van-pull-refresh v-model="isLoading" success-text="刷新成功" @refresh="onRefresh">
                    <div style="padding-bottom:40px;">
                        <router-view v-if="isRouterAlive"></router-view>
                    </div>
                </van-pull-refresh>
            </div>
        </div>
    </div>
    </div>
</template>
<script>
export default {
    name:'Sousuo',
    data:function(){
        return{
            isLoading: false,
            value:'',
            isRouterAlive: true
        }
    },
    methods: {
        onRefresh() {
            setTimeout(() => {
                this.isLoading = false;
                this.reload();
            }, 1000);
        },
        reload () {
            this.isRouterAlive = false
            this.$nextTick(() => (this.isRouterAlive = true))
                console.log(111);
        },
        onSearch:function(){
            this.$router.push('/index/indexsou/shops/'+this.value) 
        }
    },
}
</script>
<style scoped>
.top>div{
    background: #00A67C;
}

.mainMain{
    padding: 10px;
}
.mainTopDiv{
background:#00A67C;
color:#fff;
display: inline-block;
padding:0 8px;
border-radius: 3px;
}
</style>


