<template>
    <div>
        <div class="mainleft">
            <van-sidebar v-model="activeKey">
                <van-sidebar-item v-for="(item,ind) in sidebars" :key="ind" @click="fun1(item.id)" :title="item.name"/>
            </van-sidebar>
        </div>
        <div class="mainright">
            <div v-if="ifs">
                <p class="mainright1" v-if="kaishi">{{sidebars[activeKey].name}}</p>
                <van-card
                v-for="(item,ind) in lists" :key="ind"
                :desc="item.info"
                :title="item.name">
                <template #thumb>
                    <img :src="'http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/'+item.photo" style="width:60px;height:60px;">
                </template>
                <template #price>
                    <span style="color:red;font-size:16px">￥{{shoujia}}</span>
                    <div class="mainrighticon">
                    <van-icon name="add" @click="addshops(item.id)" size="24px"/>
                    </div>
                </template>
                </van-card>
            </div>
        </div>
        <div class="mainbuttom" v-if="shopif">
            <div class="mbDiv" @click="showPopup">
                <van-icon name="shopping-cart" size="26px" color="#81858A"/>
            </div>
            <div class="mbDiv1">
                <p style="color:#fff;font-size:16px; margin-left:5px;text-align:left;">￥{{zongjia}}</p>
                <p style="color:#73777C;font-size:12px;">另需配送费￥5元</p>
            </div>
            <div class="mbDiv2">
                <button class="mbDiv2Btn">￥20元起送</button>
            </div>
        </div>
        <div class="mainbuttom" v-else>
            <div class="mbDiv" @click="showPopup" style="background: #4AA478;">
                <van-icon name="shopping-cart" size="26px" color="#FBFBFB"/>
            </div>
            <div class="mbDiv1">
                <p style="color:#fff;font-size:16px; margin-left:5px;text-align:left;">￥{{zongjia}}</p>
                <p style="color:#73777C;font-size:12px;">另需配送费￥5元</p>
            </div>
            <div class="mbDiv2">
                <button class="mbDiv2Btn" style="background:#51B14D;" @click="fun2">结算</button>
            </div>
        </div>
        <div>
            <van-popup v-model="show" closeable close-icon="close" round position="bottom" :style="{ height: '30%' }">
                <h3>购物车</h3>
                <div v-if="tanchu==''">
                    <p>去选点商品加入购物车吧</p>
                </div>
                <div v-for="(item,ind) in tanchu" :key="ind" class="tanchuDiv">
                    <van-card>
                    <template #title>
                        <span style="font-size:16px;line-height:40px;">{{item.name}}</span>
                    </template>
                    <template #desc>
                        <span style="color:#F19D39;margin-left:30px;">￥9.00</span>
                        <span style="float:right;">
                        <span style="border:1px solid #ccc; float:left;margin-top:12px">
                            <button style="border:none">-</button>
                            <span style="padding:1px 3px">{{item.buyCount}}</span>
                            <button style="border:none">+</button>
                        </span>
                        <van-icon name="cross" style="float:right;margin-top:15px;margin-left:10px;" @click="deletetan(item.id)"/>
                        </span>
                    </template>
                    <template #thumb>
                        <img :src="'http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/'+item.photo" style="width:40px;height:40px;border-radius:5px;">
                    </template>
                    </van-card>
                </div>
            </van-popup>
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex';
export default {
    props: ['sid'],
    computed: {
        ...mapState(['uid']),
    },
    data:function(){
        return{
            sidebars:[],
            lists:[],
            gouwuche:[],
            tanchu:[],
            activeKey: 0,
            shoujia:9,
            zongjia:0,
            jishu:0,
            ifs:true,
            kaishi:false,
            shopif:true,
            show: false,
            zongif:true,
        }
    },
    created () {
        this.have()
    },
    methods: {
        have(){
            var app=this;
            this.$http.get('/biz/queryFoodCategory?id='+this.sid).then(function(res){
                app.sidebars=res.data;
                app.kaishi=true;
            })
            this.$http.get('/biz/queryAllFoodByShopId?shopId='+this.sid).then(function(res){
                app.lists=res.data;
            })
        },
        addshops(id){
            this.gouwuche.push({'foodId':id,"buyCount":1,"shopId":this.sid})
            for(var i=0;i<this.lists.length;i++){
                if(this.lists[i].id==id){
                    this.tanchu.push({'id':id,'name':this.lists[i].name,'photo':this.lists[i].photo,"buyCount":1})
                    this.jishu++;
                }
            }
            var sum=0;
            for(var i=0;i<this.gouwuche.length;i++){
                sum+=this.gouwuche[i].buyCount*this.shoujia;
            }
            if(sum>=20){
                this.shopif=false;
            }
            this.zongjia=sum;
        },
        fun1(id){
            var app=this;
            this.$http.get('biz/queryFoodinfoByShopIdAndFoodCategoryId?shopId='+this.sid+'&foodcategoryId='+id).then(function(res){
                app.ifs=false;
                app.lists=res.data;
                app.ifs=true;
            })
        },
        fun2(){
            if(this.uid!=null){
                var app=this;
                let params={
                    "userId":this.uid,
                    "shopId":this.sid, 
                    "list":this.gouwuche
                }
                this.$http.post('/biz/insertOrder',params).then(function(res){
                    if(res.data==1){
                        app.$toast.success('结算成功');
                        app.zongjia=0;
                        app.shopif=true;
                    }else{
                        app.$toast.fail('结算失败');
                    }
                })
            }else{
                this.$toast.fail('请登录后结算');
            }
        },
        showPopup() {
            this.show = true;
        },
        deletetan(id){
            this.shopif=false;
            for(var i=0;i<this.tanchu.length;i++){
                if(this.tanchu[i].id==id){
                    this.tanchu.splice(i, 1);
                    this.zongjia-=9;
                }
            }
            this.shopif=true;
        }
    }
}
</script>

<style scoped>
.mainleft{
    height: 25em;
    width: 21.3%;
    float: left;
    overflow-y:auto ;
}
.mainleft a{
    color:#333;
}
.mainright{
    width: 78.7%;
    float: right;
}
.mainright>div{
    height: 25.5em;
    overflow-y:auto ;
}
.mainright>div>div{
    background: #fff;
    border-bottom: 1px solid #e5e5e5;
    margin:0 15px;
    text-align: left;
}
.mainright>div>div a{
    width: 65px;
    height: 60px;
}
.mainright .van-card__title{
    font-size: 16px;
    margin-top: 10px;
}
.mainright1{
    margin:0;
    padding: 5px 15px;
    font-size: 14px;
    text-align: left;
    color:#555;
    border-left: 4px solid #e5e5e5;
    background: #F7F8FA;
}
.mainright .van-card__price{
    display: block;
}
.mainrighticon{
    color:#4AA478; 
    float:right;
}
.mainbuttom{
    position:fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 45px;
    background: #161D26;
}
.mbDiv{
    width: 40px;
    height: 40px;
    border:5px solid #161D26;
    border-radius: 50%;
    margin-top: -10px;
    line-height: 60px;
    background: #2D343B;
    margin-left: 20px;
    float: left;
}
.mbDiv1{
    float: left;
    widows: 50%;
    padding-top: 3px;
    padding-left: 10px;
}
.mbDiv1 p{
    margin:0;
}
.mbDiv2Btn{
text-align:center;
width:30%;
height:45px;
line-height:45px;
float:right;
background:#2D333A;
border:none;
color:#fff;
font-size:14px;
}
.tanchuDiv{
    margin:20px;
    border-radius: 7px;
    background: #FAFAFA;
}
.tanchuDiv>div{
    height:50px;
    text-align:left;
    margin:5px 10px;
    border-bottom:1px solid #ddd;
    padding:5px;
}
.tanchuDiv>div>div>a{
    height:50px;
    width: 50px;
}
</style>


