<template>
  <div class="hello">
    <van-notify type='primary' message='通知内容' ></van-notify>
    <div id="oneDiv">
      <h1>硅谷外卖</h1>
      <div>
        <van-tabs type="line"  v-model="active" color="#4AA478" line-width=55 title-active-color="#4AA478" class="top">
          <van-tab title="账号注册">
            <div class="center">
              <div class="topDiv1">
                <van-field label-class="text1"  center  :error="false" @blur="fun4($event,0)" class="inp1" v-model="zhuce.account" placeholder="手机/邮箱/用户名"/>
                <span style="color:red;font-size:14px;float:left;" id="topSpan1"></span>
              </div>
              <div class="topDiv1">
                <van-field label-class="text1" center  class="zhuceinp2 inp1" type="password" @blur="fun4($event,1)" v-model="zhuce.password" placeholder="请输入密码"/>
                  <van-switch class="center-inp" size="20px" v-model="checked1" @click="fun6" active-color="#4AA478" inactive-color="#dcdee0" />
              </div>
              <div class="topDiv1">
                <van-field label-class="text1" center  class="inp1" @blur="fun4($event,2)" v-model="zhuce.name" placeholder="请输入您的姓名"/>
              </div>
              <p >
                <van-button type="primary" size="large" @click="fun5" class="btn" color='#77D57B'>注 册</van-button>
              </p>
            </div>
          </van-tab>
          <van-tab title="密码登录">
            <div class="center">
              <div class="topDiv">
                <van-field label-class="text1" center  :error="false" @blur="fun2($event,0)" class="inp1" v-model="user" placeholder="手机/邮箱/用户名"/>
              </div>
              <div class="topDiv">
                <van-field label-class="text1" center  class="inp2 inp1" type="password" v-model="pwd" @blur="fun2($event,1)" placeholder="密码"/>
                  <van-switch class="center-inp" size="20px" v-model="checked" @click="fun()" active-color="#4AA478" inactive-color="#dcdee0" />
              </div>
              <div class="topDiv">
                <van-field label-class="text1" center  class="inp1 inp3" v-model="yanzheng" @blur="fun2($event,2)" placeholder="验证码"/>
                <div class="topDivDiv" @click="fun3()">
                  <sidentify style="float: right;margin-top:5px;margin-right:5px" :identifyCode="code"></sidentify>
                </div>
              </div>
              <p @click="fun2($event,4)">
                <van-button type="primary" size="large" class="btn" color='#77D57B'>登 录</van-button>
              </p>
              <span class="topa">关于我们</span>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </div>
  </div>
</template>

<script>
import sidentify from './identify'
import {mapState} from 'vuex';
      var topDiv1=document.getElementsByClassName('topDiv1');

export default {
  name: 'Index',
  components: {
    sidentify,
  },
  computed: {
    ...mapState(['uid','isShowMore']),
    isShowUid: {
      get:function(){
        return this.$store.state.uid
      },
      set:function(nval){
        this.$store.commit('mutationuid', nval)
      }
    },
    isShowName: {
      get:function(){
        return this.$store.state.name
      },
      set:function(nval){
        this.$store.commit('mutationName', nval)
      }
    },
    isShowIsLogin: {
      get:function(){
        return this.$store.state.isLogin
      },
      set:function(nval){
        this.$store.commit('mutationIsLogin', nval)
      }
    },
  },
  data:function(){
    return{
      active:1,
      user:'',
      pwd:'',
      yanzheng:'',
      checked: false,
      checked1: false,
      code:'',
      zhuce:{
        account:'',
        password:'',
        name:''
      }
    }
  },
  methods: {
    fun:function(){
      var inp2=document.getElementsByClassName('inp2')[0].getElementsByTagName('input')[0];
      if(!this.checked){
        inp2.type='text'
      }else{
        inp2.type='password'
      }
    },
    fun2:function(event,i){
      var topDiv=document.getElementsByClassName('topDiv');
      if(event.target.tagName=="INPUT"){
        if(event.target.value==""){
          topDiv[i].style.borderColor="red"
        }else{
          // topDiv[i].style.borderColor="#dddddd";
          let use=this.user;
          let pwds=this.pwds;
          let par={
            "account":use,
            "password":pwds
          }
          var app=this;
          this.$http.post('/user/login',par).then(function(res){
            if(res.data==""){
              topDiv[0].style.borderColor="red"
            }else{
              // topDiv[0].style.borderColor="#dddddd"
              if(app.pwd!=""){
                if(res.data.password==app.pwd){
                  topDiv[1].style.borderColor="#dddddd"
                }else{
                  topDiv[1].style.borderColor="red"
                }
              }else{
                  topDiv[1].style.borderColor="red"}
            }
          })
          if(i==2){
            if(this.yanzheng==this.code){
              topDiv[2].style.borderColor="#dddddd"
            }else{
              topDiv[2].style.borderColor="red"
            }
          }
        }
      }
      if(i==4){
        let use=this.user;
        let pwds=this.pwd;
        let par={
          "account":use,
          "password":pwds
        }
        var app=this;
        this.$http.post('/user/login',par).then(function(res){
          if(app.yanzheng==app.code){
            if(res.data.password==pwds){
              app.$notify(
                {type: 'success', message: '登录成功',duration: 1500, onClose(){
                  app.$router.push('/index/indexind/shops') 
                  app.isShowUid=res.data.id;
                  app.isShowName=res.data.name
                  app.isShowIsLogin=true
                }}
              )
            }else{
              app.fun3();
              app.$notify(
                {type: 'danger', message: '登录失败',duration: 1500, }
              )
            }
          }
        })
      }
    },
    fun3:function(){
      let Codes="";
      for(var i=0;i<4;i++){
      let Code=Math.floor(Math.random()*10);
      Codes+=Code;
      }
      this.code=Codes;
    },
    fun4:function(eve,i){
      if(event.target.tagName=="INPUT"){
          if(event.target.value==""){
          topDiv1[i].style.borderColor="red"
          }else{
            // topDiv[i].style.borderColor="#dddddd";
            let use=this.zhuce.account;
            let pwds=this.zhuce.password;
            let par={
              "account":use,
              "password":pwds
            }
            var app=this;
            this.$http.post('/user/login',par).then(function(res){
              if(res.data==""){
                topDiv1[0].style.borderColor="#dddddd"
                document.getElementById('topSpan1').innerHTML=""
              }else{
                topDiv1[0].style.borderColor="red"
                document.getElementById('topSpan1').innerHTML="用户名已存在"
              }
              if(i==1||i==2){
              topDiv1[i].style.borderColor="#dddddd";
              }
            })
          }
      }
    },
    fun5:function(){
      if(this.zhuce.account!=""&&this.zhuce.password!=""&&this.zhuce.name!=""){
        var app=this;
        this.$http.post('/user/register',this.zhuce).then(function(res){
          var topSpan1=document.getElementById('topSpan1')
          if(res.data=="account is exit"){
            topSpan1.innerHTML="用户名已存在"
          }else{
            document.getElementById('topSpan1').innerHTML=""
          }
          if(res.data=="register fail"){
            for(var i=0;i<3;i++){
              topDiv1[i].style.borderColor="red"
              if(topDiv1[i].children[0].children[0].children[0].children[0].value!="")
              {
                topDiv[i].style.borderColor="#dddddd"
              }
            }
          }
          if(res.data=="register success"){
            app.$notify(
                {type: 'success', message: '注册成功',duration: 2000, }
              )
          }
        })
        this.fun4();
      }
    },
    fun6:function(){
      var zhuceinp2=document.getElementsByClassName('zhuceinp2')[0].getElementsByTagName('input')[0];
      if(!this.checked1){
        zhuceinp2.type='text'
      }else{
        zhuceinp2.type='password'
      }
    },
  },
  created:function() {
    this.fun3();
  }
}
</script>

<style scoped>
*{
  margin:0;
  padding: 0;
}
#oneDiv{
  margin-top: 60px;
}
.top{
  width: 80%;
  margin:0 auto;
}
h1 {
  font-weight: 900;
  color: #4AA478;
  font-size: 48px;
}
.top>>>.van-tab__text{
  font-weight: 600;
}
.center>div{
  width: 100%;
  height: 50px;
  border:1px solid #dddddd;
  border-radius: 5px;
  margin-top: 20px;
  padding-left: 10px;
}
.center-inp{
  margin-top: 15px;
  margin-right: 15px;
  float: right;
}
.btn{
  border-radius: 5px;
  margin-top: 30px;
  font-size: 18px;
  font-weight: 500;
}
.topa{
  color:#999;
  display: block;
  font-size: 14px;
  margin-top: 20px;
}
.inp1{float: left;
  height: 50px;
  font-size: 16px;
}
.inp2{
  width: 80%;
}
.zhuceinp2 {
  width: 80%;
}
.inp3{
  width: 60%;
}
.inp1>>>.text1{
  /* border:1px solid aqua; */
  display: none;
}
</style>



