<template>
    <div>
        <div>
        <div class="top">
            <van-nav-bar 
            title="正在定位..."
            @click-left="go">
                <template #left>
                    <van-icon name="search" size="18" color="#ffffff"/>
                </template>
                <template #right>
                    <router-link to="/member">登录|注册</router-link>
                </template>
            </van-nav-bar>
        </div>
        <div class="main">
            <div class="van-hairline--bottom">
                <van-swipe>
                    <van-swipe-item>
                        <van-grid :column-num="4" :border="false">
                            <van-grid-item v-for="(item,index) in loops" :key="index" :to="'/index/indexind/shops/'+item.id" :icon="'http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/'+item.photo" :text="item.name" />
                        </van-grid>
                    </van-swipe-item>
                    <van-swipe-item>
                        <van-grid :column-num="4" :border="false">
                            <van-grid-item v-for="(item,index) in loops" :key="index" :to="'/index/indexind/shops/'+item.id" :icon="'http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/'+item.photo" :text="item.name" />
                        </van-grid>
                    </van-swipe-item>
                </van-swipe>
            </div>
            <div class="mainMain">
                <p class="mainMainP">
                    <van-icon name="wap-nav" />
                    附近商家
                </p>
                <van-pull-refresh v-model="isLoading" success-text="刷新成功" @refresh="onRefresh">
                    <div style="padding-bottom:40px;">
                    <router-view v-if="isRouterAlive"></router-view>
                    </div>
                </van-pull-refresh>
            </div>
        </div>
    </div>
    </div>
</template>
<script>
export default {
    name:'IndexInd',
    data:function(){
        return{
            loops:[],
            isLoading: false,
            isRouterAlive: true
        }
    },
    methods: {
        haveShopinte:function(){
            var app=this;
            this.$http.post('/biz/queryBigCategory').then(function(res){
                app.loops=res.data;
            })
        },
        onRefresh() {
            setTimeout(() => {
                this.isLoading = false;
                this.reload();
            }, 1000);
        },
        reload () {
            this.isRouterAlive = false
            this.$nextTick(() => (this.isRouterAlive = true))
        },
        go(){
            this.$router.push('/index/indexsou/shops') 
        }
    },
    created:function(){
        this.haveShopinte();
    }
}
</script>
<style scoped>
.top>div{
    background: #00A67C;
}
.top a{
    color:#fff;
}
.mainMain{
    padding: 10px;
}
.mainMainP{
    text-align: left;
    margin-left: 5px;
    font-size: 14px;
}
</style>


