import Vue from 'vue'
import Router from 'vue-router'
import Member from '../components/Member'
import Index from '../components/Index'
import IndexInd from '../components/IndexInd'
import IndexSou from '../components/IndexSou'
import IndexHome from '../components/IndexHome'
import IndexDing from '../components/IndexDing'
import Shops from '../components/shops'
import Details from '../components/Details'
import DetailsPing from '../components/DetailsPing'
import DetailsShang from '../components/DetailsShang'
import DetailsShop from '../components/DetailsShop'
import Vant from 'vant';
import 'vant/lib/index.css';

Vue.use(Vant);
Vue.use(Router);

const routes = [{
        path: '/',
        component: Index,
        props: true,
        children: [{
            path: '/',
            component: IndexInd,
            children: [
                { path: '/', component: Shops },
            ]
        }, ]
    },
    {
        path: '/index',
        component: Index,
        props: true,
        children: [{
                path: 'indexind',
                component: IndexInd,
                props: true,
                children: [
                    { path: 'shops', component: Shops, props: true },
                    { path: 'shops/:uid', component: Shops, props: true },
                ]
            },
            {
                path: 'indexsou',
                component: IndexSou,
                props: true,
                children: [
                    { path: 'shops', component: Shops, props: true },
                    { path: 'shops/:names', component: Shops, props: true },
                ]
            },
            {
                path: 'indexding',
                component: IndexDing,
                props: true,
                children: []
            },
            {
                path: 'indexhome',
                component: IndexHome,
                props: true,
                children: []
            },
        ]
    },
    { path: '/member', component: Member },
    {
        path: '/details/:sid',
        component: Details,
        props: true,
        children: [
            { path: '/:sid', component: DetailsShang, props: true },
            { path: 'detailsshang/:sid', component: DetailsShang, props: true },
            { path: 'detailsping/:sid', component: DetailsPing, props: true },
            { path: 'detailsshop/:sid', component: DetailsShop, props: true },
        ],
    }
]
const router = new Router({
    routes: routes
})

export default router;


