<template>
    <div>
        <div class="top">
            <div class="topleft">
                <p style="font-size:28px;margin:5px 0 0;color:#F19D39;">{{information.goodsScore}}</p>
                <p style="font-size:14px;margin:5px 0 0;">综合评分</p>
                <p style="font-size:14px;margin:5px 0; color:#999;">高于周边商家99%</p>
            </div>
            <div class="topright">
                <span class="toprightSpan">
                <van-rate :value='information.score' style="vertical-align: middle;" :size="16" color="#F19D39" void-icon="star" void-color="#eee"/>
                <span style="font-size:14px">服务态度<span style="color:#F19D39;">{{information.score}}</span></span>
                </span>
                <span class="toprightSpan">
                <van-rate :value='information.serviceScore' style="vertical-align: middle;" :size="16" color="#F19D39" void-icon="star" void-color="#eee"/>
                <span style="font-size:14px">服务态度<span style="color:#F19D39;">{{information.serviceScore}}</span></span>
                </span>
                <p style="text-align:left;font-size:14px;margin-left:20px;">送达时间<span style="margin-left:10px;color:#999;">{{information.deliveryTime}}分钟</span></p>
            </div>
        </div>
        <div style="width:100%;height:15px;border:1px solid #ddd;border-left:none;border-right:none;background:#F4F5F7;"></div>
        <div class="main">
            <div class="mainTop">
                <van-tabs v-model="active" type="card" color="#4AA478" title-inactive-color="#999" >
                    <van-tab :title="'全部'+pingjia.length" style="mainTop"></van-tab>
                    <van-tab :title="'满意'+lisgood.length" style="mainTop"></van-tab>
                    <van-tab :title="'不满意'+lisno.length" style="mainTop"></van-tab>
                </van-tabs>
            </div>
            <div class="mainButtom">
                <van-radio-group v-model="radio">
                    <van-radio name="1" checked-color="#4AA478" >只看有内容的评论</van-radio>
                </van-radio-group>
            </div>
        </div>
        <div class="buttom" v-if="ifs">
            <div v-show="active==0">
            <van-card
            v-for="(item,ind) in pingjia" :key="ind" :title="item.account" thumb="https://img01.yzcdn.cn/vant/ipad.jpeg">
                <template #title>
                    <p>
                    <span>{{item.account}}</span>
                    <span style="float:right">{{item.oederTime}}</span>
                    </p>
                </template>
                <template #thumb>
                    <img src="https://p0.ssl.img.360kuai.com/t011e3ecce76c71368b.webp" style="width:30px;height:30px;border-radius:50%;" alt="">
                </template>
                <template #desc>
                    <van-rate :value='4' style="vertical-align: middle;" :size="12" color="#F19D39" void-icon="star" void-color="#eee"/>
                    <span>{{item.score}}</span>
                    <p>{{item.comments}}</p>
                </template>
                <template #price>
                    <span>👍</span>
                    <span style="border:1px solid #ccc;padding:2px 3px;color:#777;">南瓜粥</span>
                </template>
            </van-card>
            </div>
            <div v-show="active==1">
                <van-card
                v-for="(item,ind) in lisgood" :key="ind" :title="item.account" thumb="https://img01.yzcdn.cn/vant/ipad.jpeg">
                    <template #title>
                        <p>
                        <span>{{item.account}}</span>
                        <span style="float:right">{{item.oederTime}}</span>
                        </p>
                    </template>
                    <template #thumb>
                        <img src="https://p0.ssl.img.360kuai.com/t011e3ecce76c71368b.webp" style="width:30px;height:30px;border-radius:50%;" alt="">
                    </template>
                    <template #desc>
                        <van-rate :value='item.score/10' style="vertical-align: middle;" :size="12" color="#F19D39" void-icon="star" void-color="#eee"/>
                        <span>{{item.score}}</span>
                        <p>{{item.comments}}</p>
                    </template>
                    <template #price>
                        <span>👍</span>
                        <span style="border:1px solid #ccc;padding:2px 3px;color:#777;">南瓜粥</span>
                    </template>
                </van-card>
            </div>
            <div v-show="active==2">
                <van-card
                v-for="(item,ind) in lisno" :key="ind" :title="item.account" thumb="https://img01.yzcdn.cn/vant/ipad.jpeg">
                    <template #title>
                        <p>
                        <span>{{item.account}}</span>
                        <span style="float:right">{{item.oederTime}}</span>
                        </p>
                    </template>
                    <template #thumb>
                        <img src="https://p0.ssl.img.360kuai.com/t011e3ecce76c71368b.webp" style="width:30px;height:30px;border-radius:50%;" alt="">
                    </template>
                    <template #desc>
                        <van-rate :value='item.score/10' style="vertical-align: middle;" :size="12" color="#F19D39" void-icon="star" void-color="#eee"/>
                        <span>{{item.score}}</span>
                        <p>{{item.comments}}</p>
                    </template>
                    <template #price>
                        <span>👍</span>
                        <span style="border:1px solid #ccc;padding:2px 3px;color:#777;">南瓜粥</span>
                    </template>
                </van-card>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ['sid'],
    data:function(){
        return{
            information:[],
            pingjia:[],
            lisgood:[],
            lisno:[],
            active:0,
            radio:'1',
            ifs:true,
        }
    },
    created () {
        this.have()
        this.havepingjia()
    },
    methods: {
        have(){
            var app=this;
            this.$http.get('/biz/queryInfoByShopId?shopId='+this.sid).then(function(res){
                app.information=res.data;
            })
        },
        havepingjia(){
            var app=this;
            this.$http.get('/biz/queryCommentByShopId?shopId='+this.sid).then(function(res){
                app.ifs=false;
                app.pingjia=res.data;
                app.ifs=true;
                app.shaixuan();
            })
        },
        shaixuan(){
            // if(this.active!=0){
                var lis=this.pingjia
                var lisgoodfo=[];
                var lisnofo=[];
                this.ifs=false;
                for(var i=0;i<lis.length;i++){
                    if(lis[i].isGood==1){
                        lisgoodfo.push(lis[i])
                    }
                    if(lis[i].isGood==0){
                        lisnofo.push(lis[i])
                    }
                }
                this.lisgood=lisgoodfo
                this.lisno=lisnofo
                this.ifs=true;
            // }
        }
    }

}
</script>

<style scoped>
.top{
    padding:10px;
    overflow: hidden;
}
.topleft{
    float: left;
    width: 40%;
    padding:5px;
    border-right:2px solid #ccc;
    box-sizing: border-box;
}
.topright{
    float: right;
    width: 60%;
}
.toprightSpan{
    display: block;
    margin:10px 0;
}
.main{
    padding: 20px;
    border-bottom: 1px solid #ddd;
}
.main>button{
    margin:0 5px;
    }
.mainTop .van-button{
    height: 30px;
}
.mainTop{
    width: 80%;
}
.mainButtom{
    font-size: 14px;
    margin:20px 0 0 20px;;
}
.buttom{
    text-align: left;
}
.buttom>div>div{
    background: #FFFFFF;
    margin:0 10px;
    border-bottom: 1px solid #ddd;
}   
/* .buttom .van-card{
    background: #FFFFFF;
} */
.buttom>div>div>div>a{
    margin-top:10px;
    width: 30px;
    height: 30px;
}
</style>


