import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const store = new Vuex.Store({
    state: {
        uid: null,
        name: null,
        isLogin: false
    },
    getters: {},
    mutations: {
        mutationuid: function(state, payload) {
            // state表示当前store对象的state数据，payload表示函数调用时传入的值
            state.uid = payload
        },
        mutationName: function(state, payload) {
            state.name = payload
        },
        mutationIsLogin: function(state, payload) {
            state.isLogin = payload
        },
    },
    actions: {}
})
export default store;


