<template>
    <div class="IndexMain">
        <router-view></router-view>
        <van-tabbar v-model="active">
                    <van-tabbar-item name="/index/indexind/shops" icon="home-o" @click="refresh">外卖</van-tabbar-item>
                    <van-tabbar-item name="/index/indexsou/shops" icon="search" @click="refresh">搜索</van-tabbar-item>
                    <van-tabbar-item name="/index/indexding" icon="bill-o" @click="refresh">订单</van-tabbar-item>
                    <van-tabbar-item name="/index/indexhome" icon="contact" @click="refresh">我的</van-tabbar-item>
                </van-tabbar>
    </div>
</template>
<script>
export default {
    name:'Index',
    // props: ['id'],
    data:function(){
        return{
            active: '/index/indexind/shops',
        }
    },
    methods: {
        refresh(){
            this.$router.push(this.active) 
        },
    }
}
</script>
<style scoped>
.IndexMain .van-tabbar{
    bottom: -3px;
}
</style>


