<template>
    <div class="GenDiv">
        <van-card
        num="2"
        price="2.00"
        v-for="(item,index) in shops"
        :key="index"
        @click="goDetails(item.id)"
        :thumb="'http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/'+item.photo"
        >
            <template #title>
                <div style="float:right;">
                    <van-tag type="primary" color="#F8D959" text-color="#000"><b>保</b></van-tag>
                    <van-tag type="primary" color="#F8D959" text-color="#000"><b>准</b></van-tag>
                    <van-tag type="primary" color="#F8D959" text-color="#000"><b>票</b></van-tag>
                </div>
                <van-tag type="primary" size="medium" color="#F8D959" text-color="#000"><b>品牌</b></van-tag>
                <span style="font-size:16px;"><b>{{item.name}}</b></span>
            </template>
            <template #desc>
                <div style="margin-top:5px">
                <van-rate 
                allow-half
                :value="item.goodsScore"
                :count="5" 
                gutter="1px"
                size="14px"/>
                <span>{{item.score}}</span>
                <span style="color:#F2A243;">月销量{{item.sales}}单</span>
                </div>
            </template>
            <template #price>
                <span style="font-size:13px;">￥{{item.minPrice}}起送/配送费约￥{{item.transportationPrice}}</span>
            </template>
        </van-card>
    </div>
</template>
<script>
export default {
    name:'Shops',
    props:['uid','names'],
    data:function(){
        return{
            shops:[],
        }
    },
    methods: {
        haveShops:function(){
            var app=this;
            if(this.names==undefined){
                if(this.uid!=undefined){
                    this.$http.get('/biz/queryShopsByBigId?bigcategoryId='+this.uid).then(function(res){
                        app.shops=res.data;
                    })
                }else{
                this.$http.get('/biz/queryAllShopsInfo').then(function(res){
                    app.shops=res.data;
                })
                }
            }else{
                this.$http.get('/biz/queryAllShopsInfoByName?name='+this.names).then(function(res){
                    app.shops=res.data;
                })
            }
        },
        goDetails:function(id){
            this.$router.push('/details/'+id+'/detailsshang/'+id) 
        }
    },
    created () {
        this.haveShops();
    },
    watch:{
        $route:function(to,from){
            this.haveShops();
        }
    }
}
</script>
<style scoped>
*{
    text-align:left
}
.GenDiv{
    min-height: 400px;
}
</style>


