<template>
    <div>
        <div class="top">
            <van-nav-bar>
                <template #title>
                    <span style="color:#fff">我的</span>
                </template>
            </van-nav-bar>
        </div>
        <div>
            <div class="main">
                <van-card style="text-align:left; float:left;">
                    <template #thumb>
                        <img style="width:80px;height:80px;border-radius: 50%;"
                        src="http://5b0988e595225.cdn.sohucs.com/q_70,c_zoom,w_640/images/20190129/8478c326fa174158859b9ac093e36938.jpeg" alt="">
                    </template>
                    <template #title>
                        <p style="font-size:20px;margin-bottom:5px;" v-if="isLogin">
                            <span v-if='name!=null'>{{name}}</span>
                            <span v-else>未命名</span>
                        </p>
                        <p style="font-size:20px;margin-bottom:5px;" v-else>
                            <router-link to="/member" style="color:#fff">登录|注册</router-link>
                        </p>
                    </template>
                    <template #desc>
                        <van-icon name="graphic" size="16px" style="vertical-align: middle;"/>
                        <span style="font-size:14px">暂无绑定手机号</span>
                    </template>
                    <!-- <template #num>
                    </template> -->
                </van-card>
                        <van-icon name="arrow" color='#fff' style="float:right;line-height:104px;margin-right:10px"/>
            </div>
            <van-grid :column-num="3">
                <van-grid-item class="mainMain">
                    <template #text>
                        <p>
                            <b>
                            <span class="mainSpan1" style="color:#F19D39;">0.00</span>
                            <span>元</span>
                            </b>
                        </p>
                        <p class="mainP1">我的余额</p>
                    </template>
                </van-grid-item>
                <van-grid-item class="mainMain">
                    <template #text>
                        <p>
                            <b>
                            <span class="mainSpan1" style="color:#ED6E50;">0</span>
                            <span>个</span>
                            </b>
                        </p>
                        <p class="mainP1">我的优惠</p>
                    </template>
                </van-grid-item><van-grid-item class="mainMain">
                    <template #text>
                        <p>
                            <b>
                            <span class="mainSpan1" style="color:#83C041;">0</span>
                            <span>分</span>
                            </b>
                        </p>
                        <p class="mainP1">我的积分</p>
                    </template>
                </van-grid-item>
            </van-grid>
            <div style="margin-top:10px; font-weight: 600;">
                <van-cell is-link>
                    <van-icon name="notes-o" style="vertical-align: middle;margin:0 5px;" size="16px" color="#8BC5A9" />我的订单
                </van-cell>
                <van-cell is-link>
                    <van-icon name="points" style="vertical-align: middle;margin:0 5px;" size="16px" color="#ED7053" />积分商城
                </van-cell>
                <van-cell is-link>
                    <van-icon name="award-o" style="vertical-align: middle;margin:0 5px;" size="16px" color="#F6C58A" />{{name}}的外卖会员卡
                </van-cell>
                <van-cell is-link>
                    <van-icon name="qr-invalid" style="vertical-align: middle;margin:0 5px;" size="16px" color="#56A980" />服务中心
                </van-cell>
            </div>
        </div>
    </div>
</template>
<script>
import {mapState} from 'vuex';
export default {
    name:'Index',
    computed: {
        ...mapState(['uid','name','isLogin']),
    },
}
</script>
<style scoped>
.top>div{
    background: #00A67C;
}
.main{
    overflow: hidden;
    background: #00A67C;
}
.main>div{
    background: #00A67C;
    color: #fff;
}
.mainMain p{
    margin: 5px 0;
}
.mainSpan1{
    font-size: 28px;
}
.mainP1{
    font-size: 14px;
}
</style>


