<template>
    <div>
        <div class="top">
            <van-nav-bar :border="false" @click-left="go">
                <template #left>
                    <van-icon color="#fff" size="22px" name="arrow-left" />
                </template>
                <template #title>
                    <div class="topImgDiv">
                        <div>
                        <img class="topImg" :src="'http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/'+lists.photo" alt="">
                        </div>
                    </div>
                </template>
            </van-nav-bar>
        </div>
        <div class="header">
            <div class="headerTop">
                <p style="margin-bottom:5px">
                    <van-tag type="primary" size="medium" style="vertical-align: middle;" color="#F8D959" text-color="#000"><b>品牌</b></van-tag>
                    <span style="font-size:20px;"><b>{{lists.name}}</b></span>
                    <span><van-icon name="play" size="20px" style="vertical-align: middle;"/></span>
                </p>
                <p style="font-size:13px;margin-top:10px">
                    <span>{{lists.score}}</span>
                    <span>月售{{lists.sales}}单</span>
                    <span>联想教育</span>
                    <span>约{{lists.transportationPrice}}分钟</span>
                    <span>距离{{lists.distance}}米</span>
                </p>
                <p v-if="preferential.length!=0">
                    <van-cell class="headerBow" @click="showPopup">
                        <van-tag type="primary" style="margin-top:5px;float:left;" color="#F8D959" text-color="#fff">{{sisto.tag}}</van-tag>
                        <span class="headerSpan1"> 新用户下单{{sisto.contents}}(不与其他活动同享)</span>
                        <span style="float:right;">{{preferential.length}}个优惠
                            <van-icon size="12px" name="arrow-down" />
                        </span>
                    </van-cell>
                </p>
                <p v-else>
                    <van-cell class="headerBow" @click="showPopup">
                        <span class="headerSpan1" > 该店铺暂无优惠活动</span>
                        <span style="float:right;">{{preferential.length}}个优惠
                            <van-icon size="12px" name="arrow-down" />
                        </span>
                    </van-cell>
                </p>
                <van-popup
                v-model="show"
                closeable
                close-icon="close"
                position="bottom"
                :style="{ height: '30%' }"
                class="headerBowDiv">
                    <h3>优惠活动</h3>
                    <div class="headerBowDivDiv" v-if="preferential.length!=0">
                        <p v-for="(item,ind) in preferential" :key="ind">
                            <van-tag type="primary" size="medium" color="#E38D5B" text-color="#fff">{{item.tag}}</van-tag>
                            <span>【{{item.contents}}】火烤菠萝皇堡双人餐</span>
                        </p>
                    </div>
                </van-popup>
            </div>
        </div>
        <div class="main">
            <div class="mainInd">
                <van-tabs v-model="active" title-active-color="#4AA478" color="#4AA478">
                    <van-tab title="点餐" :to="'/details/'+sid+'/detailsshang/'+sid"></van-tab>
                    <van-tab title="评价" :to="'/details/'+sid+'/detailsping/'+sid"></van-tab>
                    <van-tab title="商家" :to="'/details/'+sid+'/detailsshop/'+sid"></van-tab>
                </van-tabs>
            </div>
            <router-view></router-view>
        </div>
    </div>
</template>

<script>
export default {
    props:['sid'],
    data:function(){
        return{
            lists:[],
            preferential:[],
            sisto:{},
            show: false,
            active: 0,
        }
    },
    created () {
        this.have()
    },
    methods: {
        have(){
            var app=this;
            this.$http.get('/biz/queryInfoByShopId?shopId='+this.sid).then(function(res){
                app.lists=res.data;
            });
            this.$http.get('/biz/querySpecialOfferByShopId?shopId='+this.sid).then(function(res){
                var ifs=0;
                for(var i=0;i<res.data.length;i++){
                    if(res.data[i].tag=="首单"){
                        app.sisto=res.data[i]
                        res.data.splice(i, 1)
                        ifs=1;
                    }
                }
                if(ifs==0){
                    app.sisto=res.data[res.data.length-1]
                }
                app.preferential=res.data;
            });
        },
        showPopup() {
            this.show = true;
        },
        go(){
            this.$router.push('/index/indexind/shops')
        }
    }
}
</script>

<style scoped>
.top>div{
    background: #00A67C;
}
.top .topImg{
    width: 60px;
    height: 60px;
}
.top .topImgDiv{
    margin-top: 40px;
}
.header{
    margin-top: 30px;
}
.headerTop P{
    margin:0;
}
.headerBow{
    border:1px solid #ddd;
    width: 80%;
    font-size: 10px;
    padding: 0px 5px;
    margin:5px auto;
}
.headerBow div{
    margin-top: -2px;
}
.headerSpan1{
    width:68%;
    height: 24px;
    margin-left: 2px;
    float: left;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    display:inline-block;
}
.headerBowDiv h3{
margin:15px;
}
.headerBowDivDiv{
    text-align: left;
    padding: 0 10px;
    overflow-y: auto;
}
.headerBowDivDiv>p{
    font-size: 14px;
    margin:10px 0;
}
.headerBowDivDiv>p>.van-tag{
    margin:0 10px;
    vertical-align: middle;
}
.mainInd{
    border-bottom:1px solid #eee;
}
</style>


