<template>
    <div>
        <div class="top" >
            <van-nav-bar>
                <template #title>
                    <span style="color:#fff">订单列表</span>
                </template>
            </van-nav-bar>
        </div>
        <div>
            <div v-if="isLogin" class="main2">
                <van-tabs v-model="active">
                    <van-tab v-for="(item,index) in headers" :key="index" :title="item">
                        <van-card style="text-align:left;" v-for="(item,index) in lists" :key="index">
                        <template #thumb>
                            <img style="width:80px;height:80px;" :src="'http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/'+item.list[0].foodPhoto" alt="">
                        </template>
                        <template #title>
                            <span class="main2Span1"><b>{{item.shopName}}</b></span>
                        </template>
                        <template #price>
                            <p class="main2P1"><span class="main2Span2">
                                <span v-for="(ite,ind) in item.list" :key="ind">{{ite.foodName}},</span>
                            </span>
                            等{{item.list.length}}件商品</p>
                        </template>
                        <template #footer>
                            <van-button size="mini" round @click="deleteTo(item.id)">删除订单</van-button>
                            <van-button size="mini" round plain hairline type="info">再来一单</van-button>
                        </template>
                        </van-card>
                    </van-tab>
                </van-tabs>
                
            </div>
            <div class="main1" v-else>
                <p>登陆后查看外卖订单</p>
                <van-button color="#00A67C" to="/member">立即登录</van-button>
            </div>
        </div>
    </div>
</template>
<script>
import {mapState} from 'vuex';
export default {
    name:'Index',
    computed: {
        ...mapState(['uid','name','isLogin']),
    },
    data:function(){
        return{
            active:0,
            lists:[],
            headers:['外卖订单','待评价','退款','其他订单']
        }
    },
    methods: {
        have:function(){
            if(this.uid!=null){
                var app=this;
                this.$http.get('/biz/queryOrdersByUserId?userId='+this.uid).then(function(res){
                    app.lists=res.data;
                })
            }
        },
        deleteTo:function(pid){
                var app=this;
                this.$http.get('/biz/deleteOrderByOid?orderId='+pid).then(function(res){
                    if(res.data!=0){
                        app.have();
                    }
                })
        }
    },
    created () {
        this.have();
    }
}
</script>
<style scoped>
.top>div{
    background: #00A67C;
}
.main1{
margin-top:150px;
}
.main1>p{
    font-size:14px;
    color:#888;
}
.main1>button{
    margin-top:20px;
    border-radius: 5px;
}
.main2{
    padding-bottom: 50px;
}
.main2 .main2Span1{
font-size:16px;
margin:5px;
}
.main2 .main2Span2{
    overflow: hidden;
    max-width: 140px;
    height: 16px;
    line-height: 16px;
    margin-top: -1px;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: middle;
    display: inline-block;
}
.main2 .main2P1{
    height: 16px;
}
</style>


