<template>
    <div>
        <div class="top">
            <h3 style="margin: 0;">配送信息</h3>
            <p style="font-size:14px; margin:10px 0;">
                <van-tag mark  size="medium" type="primary" style="vertical-align: middle;">硅谷专送</van-tag>
                <span style="font-size:14px;">由商家提供配送，约{{information.deliveryTime}}分钟送达，距离{{information.distance}}米</span>
            </p>
            <p style="font-size:14px; margin:0;">配送费￥{{information.transportationPrice}}</p>
        </div>
        <div style="width:100%;height:15px;border:1px solid #ddd;border-left:none;border-right:none;background:#F4F5F7;"></div>
        <div class="top">
            <h3 style="margin: 0;">活动与服务</h3>
            <p v-for="(item,ind) in sisto" :key="ind" style="margin:10px 0;">
                <span v-if="item.tag=='首单'">
                    <van-tag type="primary" size="medium" color="#82B957">{{item.tag}}</van-tag>
                    <span style="font-size:14px;margin-left:10px;">新用户下单{{item.contents}}元(不与其他活动同享)</span>
                </span>
                <span v-else>
                    <van-tag type="primary" size="medium" color="#E38D5B">{{item.tag}}</van-tag>
                    <span style="font-size:14px;margin-left:10px;">【{{item.contents}}】火烤菠萝皇堡双人餐</span>
                </span>
            </p>
        </div>
        <div style="width:100%;height:15px;border:1px solid #ddd;border-left:none;border-right:none;background:#F4F5F7;"></div>
        <div class="top">
            <h3 style="margin: 0;">商家实景</h3>
            <div class="topImgs">
                <img v-for="i in 3" :key="i" :src="'http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/'+information.photo" alt="">
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:['sid'],
    data:function(){
        return{
            information:[],
            sisto:[]
        }
    },
    created () {
        var app=this;
            this.$http.get('/biz/queryInfoByShopId?shopId='+this.sid).then(function(res){
                app.information=res.data;
            })
            this.$http.get('/biz/querySpecialOfferByShopId?shopId='+this.sid).then(function(res){
                if(res.data.length!=0){
                    var key;
                    for(var i=0;i<res.data.length;i++){
                        if(res.data[i].tag=="首单"){
                            key=res.data.splice(i,1);
                            res.data.unshift(key[0]);
                            break;
                        }
                    }
                    app.sisto=res.data
                }
            });
    }
}
</script>

<style scoped>
.top{
    padding:15px 20px;
    text-align:left;
    border-top:1px solid #ddd;
}
.topImgs {
    margin-top:10px;
}
.topImgs img{
    width: 32%;
    margin:0 0.5%;
}
</style>


